
import { defineComponent, reactive, toRefs } from 'vue'
import { truncate } from 'lodash'

export default defineComponent({
  setup() {
    const state = reactive({
      fullDesc: false
    })

    function shorterDesc(desc: string) {
      if (!state.fullDesc) {
        return truncate(desc, { length: 400, omission: '..' })
      } else return desc
    }

    return {
      ...toRefs(state),
      shorterDesc
    }
  },
  props: {
    background: {
      type: String
    },
    description: {
      type: String
    },
    isLoading: {
      type: Boolean
    },
    name: {
      type: String
    }
  }
})
